import React from 'react'
import { useInView } from 'react-intersection-observer'

import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'

function IndexMainContent () {
    const tshld = 1

    const [ref1, inView1] = useInView({ threshold: tshld })
    const [ref2, inView2] = useInView({ threshold: tshld })
    const [ref3, inView3] = useInView({ threshold: tshld })

    const isVis1 = inView1 ? '' : 'animate__animated animate__fadeOutLeft animate__fast'
    const isVis2 = inView2 ? '' : 'animate__animated animate__fadeOut animate__fast'
    const isVis3 = inView3 ? '' : 'animate__animated animate__fadeOut animate__fast'

    return (
        <div>
            <Container fluid={true} className="index">
                <Row className="photo-container"> 
                    <Row>
                        <img src="https://purepng.com/public/uploads/large/purepng.com-manmanadult-malemale-childboy-beingmens-1421526920869cscbo.png" alt="not found"/>
                    </Row>
                    <Row className="">
                        <h2 ref={ref1} className="animate__animated animate__fadeInLeft animate_slow"> 
                            hey! my name is
                        </h2>
                        <h1 ref={ref2} className="animate__animated animate__fadeInLeft animate__slow"> 
                            davis liu
                        </h1>
                        <h4 ref={ref3} className="animate__animated animate__fadeIn animate__slow animate__delay-1s"> 
                            and I have a passion for the world of computers! 
                        </h4>
                    </Row>
                </Row>
                <Row className="skills-container">
                    <Col>
                        <img src="https://res.cloudinary.com/davis-cloud/image/upload/v1599516319/html5-without-wordmark-color-logo-14D252D878-seeklogo.com_rxx5e7.png" alt="not found"/>
                    </Col>
                    <Col>
                        <img src="https://res.cloudinary.com/davis-cloud/image/upload/v1599516318/css-118-569410_rliubz.webp" alt="not found"/>
                    </Col>
                    <Col>
                        <img src="https://res.cloudinary.com/davis-cloud/image/upload/v1599515907/J_qncj8h.png" alt="not found"/>
                    </Col>
                    <Col>
                        <img src="https://res.cloudinary.com/davis-cloud/image/upload/v1599515907/1_YU6BvZKvxivoEnvqxeG5rw_hghjld.png" alt="not found"/>
                    </Col>
                    <Col>
                        <img src="https://res.cloudinary.com/davis-cloud/image/upload/v1599516193/react_xzvqn6.png" alt="not found"/>
                    </Col>
                    <Col>
                        <img src="https://res.cloudinary.com/davis-cloud/image/upload/v1599515907/java_qcubce.png" alt="not found"/>
                    </Col>
                    <Col>
                        <img src="https://res.cloudinary.com/davis-cloud/image/upload/v1599518406/nodejslogo_paulcx.png" alt="not found"/>
                    </Col>
                </Row>
                <Row> 
                    <Col>
                        
                    </Col>
                </Row>
            </Container>
        </div>
    )
}

export default IndexMainContent